/** Media Queries **/
@use 'variables' as v;
@use 'sass:color';
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}
@mixin boton($ColorBoton, $DISPLAY ){
    background-color: $ColorBoton;
    color: v.$blanco;
    font-weight: v.$bold;
    text-decoration: none;
    padding: 1rem 3rem;
    text-align: center;
    margin-top: calc(v.$separacion / 2);
    display: $DISPLAY;
    border: none;
    &:hover{
        background-color: color.adjust($ColorBoton, $lightness: -5%);
        cursor: pointer;
    }
}